@import url(https://fonts.googleapis.com/css?family=Mulish:200,300,regular,500,600,700,800,900,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic);

* {
  font-family: "Mulish", sans-serif;
  transition: all 0.3s ease-in-out;
}

body {
  margin: 0;
  background-color: #555;
}
.App > h2 {
  color: #fff;
  text-align: center;
  background: #40005d;
  margin: 0;
  padding: 15px;
}

@media (max-width: 786px) {
  .App > h2 {
    text-align: left;
  }
}

.expense-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }

  @media (max-width: 786px) {
    .expense-date {
      width: 100%;
    }
  }
.card {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
}
.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background-color: #4b4b4b;
  margin-block: 1.2rem;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

@media (max-width: 786px) {
  .expense-item {
    flex-flow: column;
    grid-gap: 20px;
    gap: 20px;
  }
  .expense-item__description {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
  }
  .expense-item__price {
    width: 50%;
    text-align: center;
  }
}
.expenses-list {
  list-style: none;
  padding: 0;
  max-height: 50vh;
  overflow-y: auto;
}

.expenses-list__fallback {
  color: white;
  text-align: center;
}
.expenses-list::-webkit-scrollbar {
  width: .5em;
}
.expenses-list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
.expenses-list::-webkit-scrollbar-thumb {
  background-color: #4b4b4b;
  border: 1px solid #6a647c;
  border-radius: 4px;
}

.expenses-filter {
    color: white;
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }

  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }

  @media (max-width: 786px) {
    .expenses-filter__control {
      flex-flow: column;
    }
    expenses-filter__control {
      flex-direction: column;
    }
  }
.expenses {
  background-color: #222;
  max-width: 65%;
  padding: 20px;
  margin: 0 auto;
}
.expenses-error {
  color: indianred;
  padding: 0 1rem;
}
.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
  
  .new-expense__actions {
    text-align: right;
  }

  @media (max-width: 786px) {
    .new-expense__actions button{
      margin-bottom: 5px;
      width: 100%;
    }
    .new-expense__control {
      width: 100%;
    }
    .new-expense__control input{
      max-width: -webkit-fill-available;
    }
  }
.new-expense {
    background-color: #a892ee;
    padding: 1rem;
    margin: 2rem auto;
    max-width: 65%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    background-color: #510674;
    border-color: #510674;
  }
  
  .new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .new-expense button.alternative:hover,
  .new-expense button.alternative:active {
    background-color: #ddb3f8;
  }

  @media (max-width: 786px) {
    .new-expense > button {
      margin: 0
    }
  }
