.expenses-list {
  list-style: none;
  padding: 0;
  max-height: 50vh;
  overflow-y: auto;
}

.expenses-list__fallback {
  color: white;
  text-align: center;
}
.expenses-list::-webkit-scrollbar {
  width: .5em;
}
.expenses-list::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
.expenses-list::-webkit-scrollbar-thumb {
  background-color: #4b4b4b;
  border: 1px solid #6a647c;
  border-radius: 4px;
}
